body{
  background-image: url(image1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}




header{
  display: flex;
  justify-content: space-between;
  border-bottom: 6px solid white ;
}

header h1{
  line-height: 50px;
  font-size: 50px;
  width: 70%;
}
#container{
  padding-top: 40px;
  display: grid;
 width:100%;
 height:50%;

}

#table{
   width: 100%;
   border-collapse: collapse;
   font-size: 25px;

}
td{
  width:30%;
}
#td1{
  text-align: center;
  color:rgb(103, 48, 48);
}
#div{
  display: grid;
}

#setbtn{
  background-color: rgb(103, 48, 48);
  color:white;
  width:100%;
  border:0px;
  font-size: 20px;
  cursor: pointer;
}
#rmvbtn{
  background-color: black;
  color:white;
  border:0px;
  font-size: 20px;
  cursor: pointer;
}
#input{
  width:67.5%;
  font-size: 20px;
  border-style:none;
  border-bottom:4px solid rgb(7, 7, 7);
  border-radius: 5px;
  background-color: transparent;
  backdrop-filter: blur(20px);
  color:rgb(5, 5, 5);
  height: 30px;
  text-align: center;
}
#putbtn{
  width: 10%;
  height: 120%;
  background-color: rgb(103, 48, 48);
  color:white;
  border:0px;
  font-size: 20px;
  cursor: pointer;
}

#input::placeholder{
  color:white;
}